.autocompleter .loading {
    border: solid 2px #17cae6;
    border-left-color: transparent;
    border-radius: 50px;
    width: 20px;
    height: 20px;
    -webkit-animation: loading-fadeIn .3s ease-in, loading-spin 1.3s infinite ease-in-out;
            animation: loading-fadeIn .3s ease-in, loading-spin 1.3s infinite ease-in-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 1;
    margin: auto 10px;
}

@-webkit-keyframes loading-spin {
    0%, 5% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    95%, 100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes loading-spin {
    0%, 5% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    95%, 100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@-webkit-keyframes loading-fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes loading-fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.autocomplete {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
    position: relative;
    -ms-flex-align: center;
        align-items: center;
}

.autocomplete-list {
    position: absolute;
    z-index: 960;
    background-color: #fff;
    list-style: none;
    margin-left: 0;
    margin-top: 3px;
    border: 1px solid #ccc;
    float: left;
    top: 2.1rem;
    left: 0;
    overflow: auto;
    max-height: 300px;
    max-height: 50vh;
    max-width: 400px;
    max-width: 70vw;
}

.autocomplete-li {
    padding: 8px 15px;
    max-width: 400px;
    cursor: pointer;
    white-space: nowrap;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

.autocomplete-li:not(:last-child) {
    border-bottom: 1px solid #eee;
}

.autocomplete-li a {
    text-decoration: none;
    color: #444;
}

.autocomplete-li.selected {
    background: #0288D1;
    color: #fff;
}

.autocomplete-li.selected a {
    color: #fff;
}


